function KeywordChipButton({chipHandler}) {
  const clickHandler = (keyword) => {
    chipHandler(keyword);
  };

  return (
    <div>
      <ChipButtonContainer1>
          {ArrayKeyword.map((a)=>
            <ToggleChip key={a} isChecked={false} clickHandler={clickHandler}>
            {a.toString()}
            </ToggleChip>
          )}
      </ChipButtonContainer1>
    </div>
  )
}

export default KeywordChipButton;